<template>
  <div class="onebot-config-view">
    <div class="page-header">
      <h1>OneBot </h1>
      <div class="header-actions">
        <el-button @click="handleReset"></el-button>
        <el-button
          type="primary"
          :loading="saving"
          @click="handleSave"
        >
          
        </el-button>
      </div>
    </div>



    <el-card>
      <el-form
        ref="formRef"
        :model="formData"
        :rules="rules"
        label-width="160px"
        label-position="left"
      >
        <el-form-item label=" OneBot" prop="enable">
          <el-switch
            v-model="formData.enable"
            active-text=""
            inactive-text=""
          />
          <template #extra>
            <span class="form-item-tip">
               OneBot QQ 
            </span>
          </template>
        </el-form-item>

        <el-divider content-position="left"></el-divider>

        <el-form-item label=" WebSocket URL" prop="reverse_ws_url">
          <el-input
            v-model="formData.reverse_ws_url"
            placeholder="ws://localhost:8080/onebot/v11/ws"
            style="width: 500px"
            :disabled="!formData.enable"
          />
          <template #extra>
            <span class="form-item-tip">
              OneBot  WebSocket  go-cqhttp
            </span>
          </template>
        </el-form-item>

        <el-form-item label="Access Token" prop="token">
          <el-input
            v-model="formData.token"
            :type="showToken ? 'text' : 'password'"
            placeholder=" token "
            maxlength="100"
            show-word-limit
            style="width: 500px"
            :disabled="!formData.enable"
          >
            <template #append>
              <el-button
                :icon="showToken ? View : Hide"
                @click="showToken = !showToken"
              />
            </template>
          </el-input>
          <template #extra>
            <span class="form-item-tip">
               OneBot  access_token 
            </span>
          </template>
        </el-form-item>

        <el-divider content-position="left"></el-divider>

        <el-form-item label=" QQ " prop="bot_qq">
          <el-input
            v-model="formData.bot_qq"
            placeholder="2698788044"
            maxlength="20"
            style="width: 300px"
            :disabled="!formData.enable"
          />
          <template #extra>
            <span class="form-item-tip">
               QQ 
            </span>
          </template>
        </el-form-item>

        <el-form-item label=" QQ " prop="admin_qq">
          <el-input
            v-model="formData.admin_qq"
            placeholder="1099834705"
            maxlength="20"
            style="width: 300px"
            :disabled="!formData.enable"
          />
          <template #extra>
            <span class="form-item-tip">
               QQ 
            </span>
          </template>
        </el-form-item>
      </el-form>
    </el-card>

    <!--  -->
    <el-card style="margin-top: 16px;">
      <template #header>
        <div class="card-header">
          <span></span>
        </div>
      </template>
      <el-alert
        :type="formData.enable ? 'info' : 'warning'"
        :closable="false"
        show-icon
      >
        <template #title>
          {{ formData.enable ? '' : 'OneBot ' }}
        </template>
      </el-alert>
    </el-card>
  </div>
</template>

<script setup>
import { useConfigStore } from '@/stores/configStore.js';
import { Hide, View } from '@element-plus/icons-vue';
import { ElMessage } from 'element-plus';
import { onMounted, reactive, ref } from 'vue';

const configStore = useConfigStore();
const formRef = ref(null);
const saving = ref(false);
const showToken = ref(false);

// 
const formData = reactive({
  enable: false,
  reverse_ws_url: '',
  token: '',
  bot_qq: '',
  admin_qq: ''
});

// 
const originalData = reactive({});

// 
const rules = {
  reverse_ws_url: [
    {
      validator: (rule, value, callback) => {
        if (formData.enable && !value) {
          callback(new Error(' OneBot  WebSocket URL'));
        } else if (value && !value.startsWith('ws://') && !value.startsWith('wss://')) {
          callback(new Error('URL  ws://  wss:// '));
        } else {
          callback();
        }
      },
      trigger: 'blur'
    }
  ],
  bot_qq: [
    {
      validator: (rule, value, callback) => {
        if (formData.enable && !value) {
          callback(new Error(' OneBot  QQ '));
        } else if (value && !/^\d+$/.test(value)) {
          callback(new Error('QQ '));
        } else {
          callback();
        }
      },
      trigger: 'blur'
    }
  ],
  admin_qq: [
    {
      validator: (rule, value, callback) => {
        if (value && !/^\d+$/.test(value)) {
          callback(new Error('QQ '));
        } else {
          callback();
        }
      },
      trigger: 'blur'
    }
  ]
};

// 
const loadConfig = async () => {
  try {
    //  OneBot API 
    const onebotConfig = configStore.config?.onebot || await configStore.fetchConfigSection('onebot');
    
    Object.assign(formData, {
      enable: onebotConfig.enable ?? false,
      reverse_ws_url: onebotConfig.reverse_ws_url || '',
      token: onebotConfig.token || '',
      bot_qq: onebotConfig.bot_qq || '',
      admin_qq: onebotConfig.admin_qq || ''
    });
    
    // 
    Object.assign(originalData, formData);
  } catch (error) {
    ElMessage.error(' OneBot ' + error.message);
  }
};

// 
const handleSave = async () => {
  try {
    await formRef.value.validate();
    


    saving.value = true;

    await configStore.updateConfigSection('onebot', {
      enable: formData.enable,
      reverse_ws_url: formData.reverse_ws_url,
      token: formData.token,
      bot_qq: formData.bot_qq,
      admin_qq: formData.admin_qq
    });

    ElMessage.success('OneBot ');
    
    // 
    Object.assign(originalData, formData);
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('' + error.message);
    }
  } finally {
    saving.value = false;
  }
};

// 
const handleReset = () => {
  Object.assign(formData, originalData);
  formRef.value?.clearValidate();
  ElMessage.info('');
};

// 
onMounted(async () => {
  if (!configStore.config) {
    await configStore.fetchConfig();
  }
  await loadConfig();
});
</script>

